import React from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import {
  Navigation,
  Pagination,
  Scrollbar,
  A11y,
  Autoplay,
} from "swiper/modules";
// redux
import { useSelector, useDispatch } from "react-redux";
//swiper
import "swiper/scss";
import "swiper/scss/navigation";
import "swiper/scss/pagination";

export default () => {
  const productsExample = useSelector(
    (state) => state.productsSlice.slidesExample
  );

  return (
    <Swiper
      className="example-slider"
      modules={[Navigation, Pagination, Scrollbar, A11y, Autoplay]}
      autoplay={{
        delay: 5000,
        disableOnInteraction: false,
      }}
      spaceBetween={50}
      slidesPerView={3}
      pagination={{ clickable: true }}
      navigation={true}
      scrollbar={{
        hide: false, //  true,  scrollbar  ,    
        draggable: true, //    
      }}
      
      breakpoints={{
        //   >= 992px
        992: {
          slidesPerView: 3, //  3 
        },
        //    576   992px
        576: {
          slidesPerView: 2, //  2 
        },

        0: {
          slidesPerView: 1, //  1 
        },
      }}
    >
      {productsExample.map((products, i) => {
        return (
          <SwiperSlide key={products.id}>
            <div className="example__slide swiper-slide">
              <a className="example__slide-link" href="#">
                <div className="example__slide-wrapper">
                  <img src={products.url} alt=""></img>
                </div>
              </a>
            </div>
          </SwiperSlide>
        );
      })}
    </Swiper>
  );
};
